	module.exports = "<h1 class=\"heading-xlarge\">\n  Single Page<br/>Platform Development Kit\n</h1>\n\n<div class=\"text\">\n  <p class=\"lede\">\n    This Platform development kit includes and extends the GDS GOV.UK packages for developing citizen and business facing services as a platform.\n  </p>\n</div>\n<h2 class=\"heading-small heading-contents\">Contents:</h2>\n<div class=\"grid-row\">\n  <div class=\"column-two-thirds\">\n    <ul class=\"list list-contents\">\n      <li><a href=\"#pdk-principles\">Principles</a></li>\n      <li><a href=\"#pdk-packages\">Packages</a></li>\n      <li><a href=\"#pdk-contribute\">Contribute</a></li>\n    </ul>\n  </div>\n</div>\n\n<h2 class=\"heading-large\" id=\"pdk-principles\">Principles</h2>\n<div class=\"text\">\n  <p>\n    Why we created this development kit and the principles it was founded on.\n  </p>\n  <details>\n    <summary>\n      <span class=\"summary bold-small\">User driven experience</span>\n    </summary>\n    <div class=\"panel panel-border-narrow\">\n      <p>\n        Some copy TBC\n      </p>\n    </div>\n  </details>\n  <details>\n    <summary>\n      <span class=\"summary bold-small\">Domain driven design</span>\n    </summary>\n    <div class=\"panel panel-border-narrow\">\n      <p>\n        Some copy TBC\n      </p>\n    </div>\n  </details>\n  <details>\n    <summary>\n      <span class=\"summary bold-small\">Extendable governance</span>\n    </summary>\n    <div class=\"panel panel-border-narrow\">\n      <p>\n        Some copy TBC\n      </p>\n    </div>\n  </details>\n  <details>\n    <summary>\n      <span class=\"summary bold-small\">Automated assurance</span>\n    </summary>\n    <div class=\"panel panel-border-narrow\">\n      <p>\n        Some copy TBC\n      </p>\n    </div>\n  </details>\n  <details>\n    <summary>\n      <span class=\"summary bold-small\">Continuous improvement</span>\n    </summary>\n    <div class=\"panel panel-border-narrow\">\n      <p>\n        Some copy TBC\n      </p>\n    </div>\n  </details>\n  <details>\n    <summary>\n      <span class=\"summary bold-small\">Openness</span>\n    </summary>\n    <div class=\"panel panel-border-narrow\">\n      <p>\n        Some copy TBC\n      </p>\n    </div>\n  </details>\n</div>\n\n<h2 class=\"heading-large\" id=\"pdk-packages\">Packages</h2>\n\n<h2 class=\"heading-large\" id=\"pdk-contribute\" >Contribute</h2>";

