	module.exports = "<h2>Highlight text</h2>\n\n<gov-tabset>\n\n  <gov-tab heading=\"HTML Example\">\n\n    <h4 class=\"heading-small\">Default</h4>\n    <docs-example language=\"markup\">\n      <div class=\"gov-highlight-text\">\n        <p>It can take up to 8 weeks to register a lasting power of attorney.</p>\n      </div>\n    </docs-example>\n\n\n    <h4 class=\"heading-small\">Success</h4>\n    <docs-example language=\"markup\">\n      <div class=\"gov-highlight-text success\">\n        <p>It can take up to 8 weeks to register a lasting power of attorney.</p>\n      </div>\n    </docs-example>\n\n\n    <h4 class=\"heading-small\">Warning</h4>\n    <docs-example language=\"markup\">\n      <div class=\"gov-highlight-text warning\">\n        <p>It can take up to 8 weeks to register a lasting power of attorney.</p>\n      </div>\n    </docs-example>\n\n\n    <h4 class=\"heading-small\">Info</h4>\n    <docs-example language=\"markup\">\n      <div class=\"gov-highlight-text info\">\n        <p>It can take up to 8 weeks to register a lasting power of attorney.</p>\n      </div>\n    </docs-example>\n\n\n    <h4 class=\"heading-small\">Danger</h4>\n    <docs-example language=\"markup\">\n      <div class=\"gov-highlight-text danger\">\n        <p>It can take up to 8 weeks to register a lasting power of attorney.</p>\n      </div>\n    </docs-example>\n\n  </gov-tab>\n\n  <gov-tab heading=\"Angular Markup\">\n\n    <docs-example language=\"markup\">\n      <gov-page-title title=\"Here you will include the main title\">\n        <subtitle>Here you can include some extra information</subtitle>\n      </gov-page-title>\n    </docs-example>\n\n  </gov-tab>\n\n</gov-tabset>";

